import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, ModalController, LoadingController} from 'ionic-angular';
import { LoginPage} from "../login/login";
import { Storage} from "@ionic/storage";
import {BaseUI} from "../../common/baseui";
import {RestProvider} from "../../providers/rest/rest";
import { Bmobconfig} from "../../common/getBmob";
// import {HttpClient, HttpResponse} from '@angular/common/http';
import {HttpClient} from "@angular/common/http";
import { HttpClientModule } from '@angular/common/http';
/**
 * Generated class for the MorePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-more',
  templateUrl: 'more.html',
})
export class MorePage extends BaseUI{
  public loginStatus:boolean = false;
  public notLogin:boolean = true;
  headface:string;
  nickname:any;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public modalCtrl: ModalController,
              public storage: Storage,
              public loadingCtrl: LoadingController,
              public http:HttpClient) {
    super();
  }

  showModal(){
    let modal=this.modalCtrl.create(LoginPage);
    modal.present();
  }

  ionViewDidEnter() {
    this.loadUserPage();
  }

  ionViewDidLoad() {
    this.loadUserPage();
  }

  loadUserPage(){
    var Bmob = Bmobconfig.getBmob();

    this.storage.get('UserId').then(val=>{
      if(val!=null)
      {
        var loading=super.showLoading(this.loadingCtrl, "Loading...")
        this.loginStatus=true;
        this.notLogin=false;

        var currentUser = Bmob.User.current();
        var query = Bmob.Query('headimage');
        query.equalTo('owner', "==", val);
        query.find().then(list => {
          if(list[0]!=null)
          {
            console.log("show the headimage")
            var head = list[0]['headimage'];
            this.http.get(head).subscribe((f) => {
              this.headface = f['img'];
            });
          }
          else
          {
            console.log("headimage not found!")
            console.log(list[0])
          }

        });
        this.nickname = currentUser['username'];
        loading.dismiss();
      }
      else{
        this.loginStatus=false;
        this.notLogin=true;
      }
      });

  }

}



